import {ComponentProps, ReactNode, useState, useRef, useEffect} from 'react';
import clsx from 'clsx';
import {useContainerDimensions} from '~/hooks/use-container-dimensions';

interface PropsRoot extends ComponentProps<'div'> {
  children?: ReactNode;
}

export function Ticker({children, className, ...props}: PropsRoot) {
  const [isMarquee, setIsMarquee] = useState<boolean>(false);

  const componentRef = useRef<HTMLDivElement | null>(null);
  const marqueeRef = useRef<HTMLDivElement | null>(null);
  const {width: widthContent} = useContainerDimensions(componentRef);
  const {width: widthMarquee} = useContainerDimensions(marqueeRef);

  useEffect(() => {
    const checkMarquee =
      typeof widthContent !== undefined &&
      typeof widthMarquee !== undefined &&
      widthContent > widthMarquee;
    setIsMarquee(checkMarquee);
  }, [widthContent, widthMarquee]);

  return (
    <div
      ref={marqueeRef}
      className={clsx('flex overflow-x-hidden ', className)}
    >
      <div className={isMarquee ? 'animate-marquee whitespace-nowrap' : ''}>
        <div ref={componentRef} className="whitespace-nowrap">
          {children}
        </div>
      </div>
    </div>
  );
}
